:root {
  --backgroundColour: #221F2D;
  --backgroundColourSecondary: #242130;
  --contentBackgroundColour: #353549;
  --textColour: #fff;
  --textColourSecondary: #f2f2f2;
  --headerFont: 'roboto', helvetica, arial;
  --bodyFont: 'roboto', helvetica, arial;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/* Columns */
.col {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  height: 100vh;
}

.header {
  width: 100%;
  background: var(--contentBackgroundColour);
}

label {
  display: block;
  margin-top: 2em;
  text-align: left;
}

input {
  display: block;
  background: transparent;
  font-size: 1rem;
  border: solid 1.5px #fff;
  border-radius: 5px;
  line-height: 1.5;
  padding: 0.5em 1em;
  color: var(--textColourSecondary);
  font-weight: 300;
  margin-top: 2em;
  width: 100%;
}

input.valid {
  border: solid 1.5px #82DDA9;
}

input.invalid {
  border: solid 1.5px #ff6644;
}

.btn {
  display: inline-block;
  color: var(--contentBackgroundColour);
  line-height: 1.5;
  font-size: 1rem;
  padding: 0.5em 1em 0.4em;
  background: #fff;
  border: solid 1.5px #fff;
  cursor: pointer;
  font-weight: bold;
}

.submit-btn {
  margin-top: 0.5em;
  border-radius: 5px;
}

h2 {
  font-weight: 300;
}

ul {
  display: -ms-flexbox;
  display: flex;
}

.colour-list {
  display: grid;
  grid-template-columns: repeat(4, minmax(120px, 150px));
  grid-column-gap: 2.5em;
  grid-row-gap: 2.5em;
  position: relative;
}

.colours {
  margin-left: 2.5rem;
}

.invisible {
  opacity: 0;
  -webkit-transform: rotateX(-90deg);
  transform: rotateX(-90deg);
}

.hidden {
  display: none;
}

.mouse-enabled *:focus {
  outline: none;
}

.colour-list:hover .colour-add {
  opacity: 1;
}



